.currency-carousel {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 50%;
    width: 100%;
    color: #ffffff;
}

.currency-carousel .currency-carousel-container {
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -webkit-transition: margin-left 200ms ease-in-out;
    -o-transition: margin-left 200ms ease-in-out;
    transition: margin-left 200ms ease-in-out;
}

.currency-carousel .dot-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-bottom: 1em;
}

.currency-carousel .dot-container .dot {
    border: 3px solid;
    border-radius: 50%;
    border-color: rgba(255, 255, 255, 0.5);
    margin-right: 5px;
    -webkit-transition: all 200ms ease-in-out;
    -o-transition: all 200ms ease-in-out;
    transition: all 200ms ease-in-out;
}

.currency-carousel .dot-container .dot.active {
    border-color: rgb(255, 255, 255);
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}

.currency-carousel.target {
    background-image: -webkit-gradient(linear, right top, left bottom, color-stop(10%, #b73cb5), to(#0b58b2));
    background-image: -webkit-linear-gradient(top right, #b73cb5 10%, #0b58b2);
    background-image: -o-linear-gradient(top right, #b73cb5 10%, #0b58b2);
    background-image: linear-gradient(to bottom left, #b73cb5 10%, #0b58b2);
}



