.header-button {
    display: -ms-flexbox;
    display: flex;
    background-color: transparent;
    border: rgba(255, 255, 255, 0.2) solid 1px;
    color: #ffffff;
    border-radius: 5px;
    padding: 0.4em 0.4em;
    margin: 0.5em;
    cursor: pointer;
    -webkit-transition: all ease-in-out 200ms;
    -o-transition: all ease-in-out 200ms;
    transition: all ease-in-out 200ms;
    outline: none;
}

.header-button::after {
    content: url(./assets/down-arrow.svg);
    width: 0.8em;
    height: 0.8em;
    margin-left: 0.5em;
}

.header-button:hover {
    border-color: rgba(255, 255, 255, 0.6);
    background-color: rgba(59, 81, 179, 0.3);
}

.header-button:active {
    border-color: rgba(255, 255, 255, 0.9);
    background-color: rgba(59, 81, 179, 0.7);
}

.header-button .currency-presenter {
    margin: 0 0.3em;
}



